:has(.tooltip) {
  position: relative;
}

.tooltip {
  background-color: var(--color-gray-5);
  color: var(--color-white);
  z-index: 1;
  border-radius: 4px;
  width: 10rem;
  padding: .6rem;
  font-size: .8em;
  font-weight: normal;
  position: absolute;
  top: -100%;
